body {
    background-color: rgba(210, 245, 255, 0.26);
    background-image: url('./images/corona3bg.png');
}

.container{
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    
}

.image{
    width: 370px;
    margin-top: 10px;
    margin-bottom: 20px;
}

@media (max-width: 770px) {
    .container{
        margin: 0 10%;
    }
    .image{
        width: 100%;
    }
}