.container{
    margin: 50px 0;
}

.card{
    margin: 2% 1% !important;
    padding: 0 5px !important;
    
    

}

.infected{
    border-bottom: 15px solid rgba(0, 0, 255, 0.5)
}

.recovered{
    border-bottom: 15px solid rgba(0, 255, 0, 0.5)
}

.deaths{
    border-bottom: 15px solid rgba(255, 0, 0, 0.5)
}

@media (max-width: 770px){
    .card{
        margin: 2% 0 !important;

    }

}